.header_container{
 position: fixed;
 top: 0;
 width: 100%;
 padding: 20px;
 height: 30px;
 z-index: 1;
 display: flex;
transition: all 0.5s;
transition-timing-function: ease-in;
color: white;
}

.header_left ul{
 display: flex;
 top: 10px;
position: absolute;
  justify-items: center;
}
ul li{
 list-style-type: none;
 padding: 5px;
 cursor: pointer;
}
.header_black{
 background-color: #111;
} 
.header_right ul{
 display: flex;
 align-items: center;
  position: fixed;
 top: 10px;
 right: 20px;
}
.header_logo{
 left: 20px;
 width: 80px;
 object-fit: contain;
}
.header_avatar{
 position: fixed;
 right: 20px;
 width: 30px;
 object-fit: contain;
}